    export default {
        name: 'app-model',
	    props: {
            type: {
                type: String,
                default: function() {
                    return '1';
                }
            },
		    background: {
                type: String,
			    default: function() {
			        return 'white';
			    }
		    },
		    height: {
                type: Number,
			    default: function() {
			        return 500;
			    }
		    },
		    value: {
                type: Boolean,
			    default: function() {
			        return false;
			    }
		    }
	    },
	    data() {
            return {
                display: this.value
            }
        },
        methods: {
            bubble() {
                return false;
            },
            close() {
                this.display = false;
                this.$emit('input', this.display);
            },
        },
        computed: {
            setHeight() {
                if (this.display === true) {
                    return `0`;
                } else {
                    return `-${this.height + 108}rpx`;
                }
            }
        },
        watch: {
            value: function() {
                this.display = this.value;
            }
	    }
    }
